html,body{
    margin:0;
    padding: 0;
    font-family: 'Rubik',sans-serif;
    background-color: #eef0f4;

}
.container{
    display: flex;
    flex-direction: column;
    max-width: 320px;
    margin: 30px auto;
}
img{
    width: 150px;
    margin: 0 auto;
}

input{
    color: #432000;
    background-color: #dce1eb;
    border: 0;
    padding: 15px;
    border-radius: 8px;
    font-size: 20px;
    font-family: 'Rubik',sans-serif;
    text-align: center;
    margin: 10px 0px;
    
}

button{
    color:#FDFDFD;
    background-color: #ac485a;
    border: 0;
    padding: 15px;
    border-radius: 8px;
    font-size: 20px;
    font-family: 'Rubik',sans-serif;
    text-align: center;
}
button:hover{
    background-color: #432000;
    cursor: pointer;
}
ul{
    list-style-type: none;
    display: flex;
    padding: 0;
    flex-wrap: wrap;
    gap:10px;
}

ul li{
    background-color: #e7d29c;
    box-shadow: 0px 1px 4px rgba(0, 0, 0,0.2);
    font-size: 20px;
    padding: 15px;
    border-radius: 8px;
    flex-grow:1;
    text-align: center;
}
ul li:hover{
    background-color: #dbaa47;
    cursor: pointer;
}